import { AfterViewInit, Component, OnDestroy, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { MenuController } from '@ionic/angular';
import { ApicallService } from 'src/app/services/apicall.service';
import { GlobalService } from 'src/app/services/global.service';
import { ToastService } from 'src/app/services/toast.service';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.page.html',
  styleUrls: ['./profile.page.scss'],
})
export class ProfilePage implements OnInit, OnDestroy, AfterViewInit {
  sowButton: boolean = false;
  enableInput : boolean = true;
  changeIcon: boolean = true;
  public user:any= {u_id:'', username:'', password:''}
  constructor(public menuCtrl: MenuController, private router:Router, public apicall : ApicallService, public global : GlobalService, public toast : ToastService )
   { 
    this.menuCtrl.enable(true);
   }

  ngOnInit() {
    this.menuCtrl.enable(true);
    this.global.AdminprofileData.subscribe(res => {
      this.user = res.user
      console.log(res);
      console.log(this.user);
    });
  }
  ngAfterViewInit(){
    console.log('AfterViewInit');
    this.menuCtrl.enable(true);
  }
  ngOnDestroy() {
    console.log('Destroy');
    this.menuCtrl.enable(true);
  }

  enable(){
    this.changeIcon = !this.changeIcon;
    this.enableInput = ! this.enableInput;
    this.sowButton = ! this.sowButton;
  }


  login(){
    console.log(this.user);
    this.apicall.api_updateadminProfile(this.user);
  }
  goToRegister(){
    this.router.navigate(['register'])
  }
  backtomenu(){
    this.router.navigate(['menu'])
  }



}
